<template>
  <h1 v-if="list.length">{{menu_show}}</h1>
</template>
<script>
export default {
  name: "iron_template",
  // data() {
  //   return {
  //     menu: [
  //       { num: 1, type: "a", title: "apple" },
  //       { num: 2, type: "a", title: "abc" },
  //       { num: 3, type: "a", title: "aaaaa" },
  //       { num: 4, type: "b", title: "bird" },
  //       { num: 5, type: "b", title: "bbb" },
  //       { num: 6, type: "c", title: "ccc" },
  //       { num: 7, type: "c", title: "card" }
  //     ],
  //   };
  // },
  computed: {
    now_index:{
      get(){
        return this.$store.state.index
      },
      set(val){
        this.$store.commit('SETINDEX',val)
      }
    },
    menu_show() {
      return this.list[this.now_index].title;
    },
    list(){
      // console.log(this.$store)
      return this.$store.state.list
    }
  },
  mounted() {
    let index = parseInt(this.$route.params.index);

    this.$store.dispatch("GETLIST",index)
  },
  watch: {
    $route() {
      // console.log(odd)
      // console.log(now)
      this.now_index = parseInt(this.$route.params.index);
    }
  }
};
</script>